import { computed, inject, reactive } from "vue";
import { useStore } from "vuex";

export function useDialogEmbryoDetailForm() {
    const store = useStore();

    const selectedEmbryo = inject("selectedEmbryo");
    const embryo = inject("embryoDetail");

    const form = reactive({
        fertLineId: null,
        dayNo: 0,
        oocId: null,
        cultureDay: null,
        embryoAge: null,
        stage: null,
        grade: null,
        score: null,
        time: null,
        oocGroup: null,
        clinician: null,
        embryologist: null
    });

    const stageOptions = computed(() => {
        if (selectedEmbryo.day == 0) {
            return store.state.fertilization.maturityList.map((item) => ({
                label: item,
                value: item
            }));
        } else {
            return store.state.fertilization.stageList.map((item) => ({
                label: item,
                value: item
            }));
        }
    });

    const clinicianOptions = computed(() => store.state.provider.providers);
    const embryologistOptions = computed(() => store.state.provider.providers);

    return {
        embryo,
        selectedEmbryo,
        stageOptions,
        clinicianOptions,
        embryologistOptions
    };
}
